import { Injectable } from '@angular/core';
import {
  Router,
  Resolve,
  RouterStateSnapshot,
  ActivatedRouteSnapshot,
} from '@angular/router';
import { Observable } from 'rxjs';
import { map } from 'rxjs/operators';

import {
  GetProductDetailsGQL,
  GetProductDetailsQuery,
  ProductStatus,
} from '../../../graphql/graphql';

@Injectable()
export class ProductResolver
  implements Resolve<GetProductDetailsQuery['product']> {
  constructor(
    private getProductDetailsGQL: GetProductDetailsGQL,
    private router: Router,
  ) {}

  resolve(
    route: ActivatedRouteSnapshot,
    state: RouterStateSnapshot,
  ):
    | GetProductDetailsQuery['product']
    | Observable<GetProductDetailsQuery['product']>
    | Promise<GetProductDetailsQuery['product']>
    | null
    | undefined {
    const id = parseInt(route.paramMap.get('id') || '', 10);
    if (id) {
      return this.getProductDetailsGQL
        .fetch(
          {
            where: {
              id,
            },
          },
          {
            fetchPolicy: 'network-only',
          },
        )
        .pipe(
          map((result) => {
            if (result.error || result.errors) {
              this.router.navigateByUrl('/exception/500');
              return undefined;
            }

            if (result.data?.product) {
              return result.data.product;
            } else {
              this.router.navigateByUrl('/exception/404');
              return undefined;
            }
          }),
        );
    } else {
      return {
        status: ProductStatus.Active,
        translations: [
          {
            imageDescription:
              '<html><head><style>img {width: 100%; height: auto;} iframe { width: 96vw; height: calc(96vw / 1.77); } p {margin: 0px; padding: 0px;}</style></head><body></body></html>',
          },
        ],
      } as GetProductDetailsQuery['product'];
    }
  }
}
