import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { PagesComponent } from './pages.component';

const routes: Routes = [
  {
    path: '',
    component: PagesComponent,
    children: [
      { path: '', pathMatch: 'full', redirectTo: '/welcome' },
      {
        path: 'welcome',
        loadChildren: () =>
          import('../welcome/welcome.module').then((m) => m.WelcomeModule),
      },
      {
        path: 'events',
        loadChildren: () =>
          import('../event/event.module').then((m) => m.EventModule),
      },
      {
        path: 'products',
        loadChildren: () =>
          import('../product/product.module').then((m) => m.ProductModule),
      },
      {
        path: 'brands',
        loadChildren: () =>
          import('../brand/brand.module').then((m) => m.BrandModule),
      },
      {
        path: 'categories',
        loadChildren: () =>
          import('../category/category.module').then((m) => m.CategoryModule),
      },
      {
        path: 'feed',
        loadChildren: () =>
          import('../feed/feed.module').then((m) => m.FeedModule),
      },
      {
        path: 'reviews',
        loadChildren: () =>
          import('../review/review.module').then((m) => m.ReviewModule),
      },
      {
        path: 'ingredients',
        loadChildren: () =>
          import('../ingredient/ingredient.module').then(
            (m) => m.IngredientModule,
          ),
      },
      {
        path: 'functions',
        loadChildren: () =>
          import('../function/function.module').then((m) => m.FunctionModule),
      },
      {
        path: 'cautions',
        loadChildren: () =>
          import('../caution/caution.module').then((m) => m.CautionModule),
      },
      {
        path: 'posts',
        loadChildren: () =>
          import('../feed/feed.module').then((m) => m.FeedModule),
      },
      {
        path: 'live-streams',
        loadChildren: () =>
          import('../live-stream/live-stream.module').then(
            (m) => m.LiveStreamModule,
          ),
      },
      {
        path: 'users',
        loadChildren: () =>
          import('../user/user.module').then((m) => m.UserModule),
      },
    ],
  },
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule],
})
export class PagesRoutingModule {}
